@font-face {
  font-family: "PT Sans";
  font-display: swap;
  font-weight: 400;
  font-style: normal;
  src: local("PT Sans"), local("PTSans-Regular"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff"); }

@font-face {
  font-family: "PT Sans";
  font-display: swap;
  font-weight: 700;
  font-style: normal;
  src: local("PT Sans Bold"), local("PTSans-Bold"), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

body {
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  margin: 0;
  padding: 0;
  position: relative;
  min-width: 320px;
  width: 100%;
  margin: 0 auto;
  font-family: "Open Sans", "Arial", sans-serif;
  background-color: #2f4b76;
  color: #000000;
  overflow-x: hidden; }

section {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  overflow-x: hidden; }

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  position: relative; }

.container img {
  height: 100%;
  width: 100%; }

.all-tooltip {
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0; }

.tooltip-content {
  position: absolute;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
  max-width: 200px;
  opacity: 0;
  pointer-events: none;
  z-index: 2; }

.tooltip-content .arrow {
  position: absolute;
  width: 10px;
  height: 10px;
  border: 10px solid transparent;
  border-bottom-color: white;
  top: 0px;
  left: 50%;
  transform: translate(-50%, -100%) rotate(0deg); }

.pin {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 20px;
  width: 20px;
  background-color: white;
  border-radius: 50%;
  cursor: pointer; }
  .pin::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: white;
    border-radius: 50%;
    -webkit-animation: puls 1s ease infinite;
            animation: puls 1s ease infinite; }
    .tooltip:nth-child(2n) .pin::after {
      -webkit-animation-delay: 0.3s;
              animation-delay: 0.3s; }
    .tooltip:nth-child(3n) .pin::after {
      -webkit-animation-delay: 0.6s;
              animation-delay: 0.6s; }
    .tooltip:nth-child(4n) .pin::after {
      -webkit-animation-delay: 0.9s;
              animation-delay: 0.9s; }

.tooltip.active .tooltip-content {
  opacity: 1;
  pointer-events: all;
  display: block; }

.tooltip-1 .pin {
  top: 75%;
  left: 20%; }

.tooltip-2 .pin {
  top: 37%;
  left: 56%; }

.tooltip-3 .pin {
  top: 69%;
  left: 60%; }

.tooltip-4 .pin {
  top: 27%;
  left: 73%; }

.tooltip-5 .pin {
  top: 32%;
  left: 89%; }

@-webkit-keyframes puls {
  0% {
    transform: scale(1);
    opacity: 1; }
  100% {
    transform: scale(2);
    opacity: 0; } }

@keyframes puls {
  0% {
    transform: scale(1);
    opacity: 1; }
  100% {
    transform: scale(2);
    opacity: 0; } }

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  color: #4a4a4a;
  padding: 35px 20px 20px;
  background-color: #ffffff; }

.content__title {
  font-size: 16px;
  line-height: 20px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 5px;
  color: #212e4e; }

.content__text {
  margin-bottom: 5px; }

.content__price {
  font-weight: 700;
  font-size: 20px;
  line-height: 23px;
  font-weight: 700;
  margin-bottom: 10px; }
  .content__price--old {
    font-size: 10px;
    line-height: 13px;
    font-weight: 700;
    margin-bottom: 5px;
    color: #8b8b8b;
    text-decoration: line-through; }
    .content__price--old ~ .content__price {
      color: #c41010; }

.content__button {
  font-size: 14px;
  line-height: 16px;
  font-weight: 400;
  background: linear-gradient(to right top, #212e4e, #1450aa);
  color: #ffffff !important;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: block;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  transition: 0.3s ease-in-out; }
  .content__button:active {
    color: #cac9c9 !important; }

.bubble {
  position: absolute;
  background-color: #ffffff;
  transform: translate(-50%, -50%);
  pointer-events: none;
  border-radius: 50%;
  -webkit-animation: bubbles 1s linear infinite;
          animation: bubbles 1s linear infinite; }

@-webkit-keyframes bubbles {
  0% {
    width: 0;
    height: 0;
    opacity: 0.03; }
  100% {
    width: 100px;
    height: 100px;
    opacity: 0; } }

@keyframes bubbles {
  0% {
    width: 0;
    height: 0;
    opacity: 0.03; }
  100% {
    width: 100px;
    height: 100px;
    opacity: 0; } }

.content__info {
  font-size: 11px;
  line-height: 14px;
  font-weight: 700;
  position: absolute;
  content: "";
  top: 0px;
  right: 0;
  color: #ffffff;
  padding: 6px 10px; }
  .content__info--new {
    background-color: #d28f4d; }
  .content__info--offer {
    background-color: #a616da; }
  .content__info--sale {
    background-color: #c41010; }
